.block-editor-block-list__block .waldos-friends-author {
  display: flex;
}
.block-editor-block-list__block .waldos-friends-author img {
  border-radius: 50%;
  width: 80px;
  height: 80px;
}
.block-editor-block-list__block .waldos-friends-author__description {
  display: flex;
  flex-direction: column;
  margin-left: 16px;
}
.block-editor-block-list__block .waldos-friends-author__description h3 {
  padding: 0px;
  margin: 0px;
  font-size: 24px;
  color: #313131;
  font-weight: 600;
}
.block-editor-block-list__block .waldos-friends-author__description p {
  padding: 0px;
  margin: 0px;
  font-size: 18px;
  color: #313131;
  font-weight: 400;
}